              <el-option label="" value="wx"></el-option>
              <el-option label="" value="ali"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formData.orderNum" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="refund"></el-button>
          </el-form-item>
        </el-form>
        <el-form :inline="true" :model="formData" class="demo-form-inline">
          <el-form-item label="">
            <el-select v-model="formData.orderType" placeholder="">
              <el-option label="" value="wx"></el-option>
              <el-option label="" value="ali"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formData.orderNum" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="cancel"></el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <!---->
      <el-tab-pane label="" name="preCreate">
        <el-form :inline="true" :model="formData" class="demo-form-inline">
          <el-form-item>
            <div class="qr-box">
              <canvas id="qrcode"></canvas>
            </div>
            <el-button type="primary" @click="preCreate"></el-button>
          </el-form-item>
        </el-form>
        <el-form :inline="true" :model="formData" class="demo-form-inline">
          <el-form-item label="">
            <el-select v-model="formData.orderType" placeholder="">
              <el-option label="" value="wx"></el-option>
              <el-option label="" value="ali"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formData.orderNum" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="query"></el-button>
          </el-form-item>
        </el-form>
        <el-form :inline="true" :model="formData" class="demo-form-inline">
          <el-form-item label="">
            <el-select v-model="formData.orderType" placeholder="">
              <el-option label="" value="wx"></el-option>
              <el-option label="" value="ali"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formData.orderNum" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="refund"></el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <!--jsapi-->
      <el-tab-pane label="/" name="jsPay">
        <el-form :inline="true" :model="formData" class="demo-form-inline">
          <div class="qr-box">
            <canvas id="js_qrcode"></canvas>
          </div>
          <el-form-item label="">
            <el-select v-model="formData.orderType" placeholder="">
              <el-option label="" value="wx"></el-option>
              <el-option label="" value="ali"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="jsPay"></el-button>
            <!--<el-button type="primary" @click="aliJsPay"></el-button>-->
          </el-form-item>
        </el-form>
        <el-form :inline="true" :model="formData" class="demo-form-inline">
          <el-form-item label="">
            <el-select v-model="formData.orderType" placeholder="">
              <el-option label="" value="wx"></el-option>
              <el-option label="" value="ali"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="formData.orderNum" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="query"></el-button>
          </el-form-item>
        </el-form>
        <el-form :inline="true" :model="formData" class="demo-form-inline">
          <el-form-item label="">
            <el-select v-model="formData.orderType" placeholder="">
              <el-option label="" value="wx"></el-option>
              <el-option label="" value="ali"></el-option>
