import request from '@/utils/request'
import qs from 'qs'

// 
export function preCreate(orderType) {
  // Promise
  return request({
    url: '/fuiou/admin/pre_create',
    method: 'post',
    data: qs.stringify({
      orderType: orderType,
    })
  })
}
// jsapi
export function jsPay(orderType) {
  // Promise
  return request({
    url: '/fuiou/admin/jspay',
    method: 'post',
    data: qs.stringify({
      orderType: orderType,
    })
  })
}


